/*"\frontend\scorch-web-app\src\components\Navbars\TopNavBar\BackButton\BackButton.css" */
.backButton {
    position: absolute;
    left: 90%;
    transform: translateX(-50%);
    background-color: #0d52c7;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s;

    &:hover {
        background-color: #e5e5e5;
    }
}

@media (max-width: 768px) { 
    .backButton {
        
